  top: 0;
}

.newyearad .win {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: fit-content;
}

.newyearad .win .nr {
  width: 100%;
  height: 100%;
}


.newyearad .win .nr img {
  max-width: 500px;
  width: 100%;

}


.newyearad .win .close {
  position: absolute;
  font-style: normal;
  width: 36px;
  height: 36px;
  border: solid 1px #eddfae;
  border-radius: 100%;
  right: -18px;
  top: -18px;
  cursor: pointer;
  font-size: 32px;
  line-height: 24px;
  text-align: center;
